import React from 'react'
import Links from './Links'
const Footer = () => {
    return (
        <footer>
            <div className="m-3 grid grid-cols-4 max-w-max mx-auto">
                <Links />
            </div>
            <p className="text-center p-5 font-bold bg-green-800 text-white">Copyright &copy; 2021 - Nombre Apellido</p>
        </footer>
    )
}

export default Footer
